class Skill {
  constructor(
    name,
    type,
    power,
    accur,
    pp,
    prior,
    touch,
    stype,
    dtype,
    skillEffectList,
    skillRequirement
  ) {
    this.name = name;
    this.power = power;
    this.type = type;
    this.accur = accur;
    let ppp = Math.floor(pp / 5) * 3 + pp;
    this.pp = ppp;
    this.prior = prior;
    this.touch = touch;
    this.stype = stype; //atk = , catk = , natk =   , buf =   
    this.defCdef = dtype;
    this.skillEffectList = skillEffectList;
    this.skillRequirement = skillRequirement;
  }
}

