import React from "react";

const HpBar = ({ hp, maxHp }) => {
  // HP    (hp/maxHp )
  let hpWidth = (hp / maxHp) * 100; //   
  if (hp <= 0) {
    hpWidth = 0;
  }

  return (
    <div
      className="current-hp"
      style={{
        width: `${hpWidth}%`, // HP  
      }}
    ></div>
  );
};

export default HpBar;
