export const rank = (battle, enqueue, rankPokemon, rankType, rankValue) => {
  let pokemon;
  if (rankPokemon === "npc") {
    pokemon = battle.npc;
  } else if (rankPokemon === "player") {
    pokemon = battle.player;
  }
  let rank = pokemon.tempStatus.rank;
  let rankText = pokemon.name + " " + getStatName(rankType); //''     !
  let rankText2 = pokemon.name + " " + getStatName2(rankType); //''  !

  if (rank[rankType] === -6 && rankValue < 0) {
    rankText2 = rankText2 + "     !";
    enqueue({ battle, text: rankText2 });
    return;
  } else if (rank[rankType] === 6 && rankValue > 0) {
    rankText2 = rankText2 + "     !";
    enqueue({ battle, text: rankText2 });
    return;
  }

  rank[rankType] += rankValue;
  if (rank[rankType] > 6) {
    rank[rankType] = 6;
  }
  if (rank[rankType] < -6) {
    rank[rankType] = -6;
  }
  const multiplier = getMultiplier(rank[rankType]);
  pokemon[rankType] = Math.floor(pokemon.origin[rankType] * multiplier);

  if (rankValue > 2 || rankValue < -2) {
    rankText = rankText + " ";
  }
  if (rankValue === 2 || rankValue === -2) {
    rankText = rankText + " ";
  }
  if (rankValue > 0) {
    rankText = rankText + " !";
  }
  if (rankValue < 0) {
    rankText = rankText + " !";
  }
  enqueue({ battle, text: rankText });
};

const getMultiplier = (rank) => {
  if (rank >= 1 && rank <= 6) {
    return 1 + rank * 0.5; // : 1  1.5, 2  2
  } else if (rank <= -1 && rank >= -6) {
    return 1 / (Math.abs(rank) * 2 + 1); // : -1  2/3, -2  1/2
  } else if (rank === 0) {
    return 1;
  }
  console.error("   ");
  return 1; // rank 0    
};

const getStatName = (stat) => {
  switch (stat) {
    case "atk":
      return "";
    case "def":
      return "";
    case "catk":
      return " ";
    case "cdef":
      return " ";
    case "speed":
      return "";
    default:
      return "  "; //      
  }
};

const getStatName2 = (stat) => {
  switch (stat) {
    case "atk":
      return "";
    case "def":
      return "";
    case "catk":
      return " ";
    case "cdef":
      return " ";
    case "speed":
      return "";
    default:
      return "  "; //      
  }
};
